import React, { Component } from 'react';
import ReactSpeedometer from "react-d3-speedometer"
import {Line} from 'react-chartjs-2';
import Chart from 'chart.js';
import {  withStyles } from '@material-ui/core/styles';
import Grid from "@material-ui/core/Grid";
import Card from '@material-ui/core/Card';
import Typography from '@material-ui/core/Typography';

const styles = theme => ({
  Chart:{
    width:500,
    height:200,
  },
  grid:{
    marginTop: 15
  },
  grid2: {
    //padding: "0 15px !important",
    //marginBottom: 15,
  },
  title: {
    fontSize: 24,
    padding: "10px",
    marginTop: 2,
  },
  space:{
      padding: "10px",
      align: "Center",
      width: 350
  }
})

/*                  {
                    label:"TE3801",
                    data:this.props.test.val1,
                    fill:false,
                    borderColor : 'rgba(0, 0, 255, 0.6)',
                    backgroundColor : 'rgba(0, 0, 255, 0.6)'
                  }*/
                 
class RTChart extends Component{
    chartReference = {};
    constructor(props){
        super(props);
        this.state = {
            chartData: {
                labels:this.props.test.dt,
                datasets:[
                  {
                    label:"PT3801",
                    data:this.props.test.val1,
                    fill:false,
                    borderColor : 'rgba(148, 0, 211, 0.6)',
                    backgroundColor : 'rgba(148, 0, 211, 0.6)'
                  },

                ]
            },
            chartData2:{
              labels:this.props.test.dt,
                datasets:[
                  {
                    label:"TE3801",
                    data:this.props.test.val2,
                    fill:false,
                    borderColor : 'rgba(148, 0, 211, 0.6)',
                    backgroundColor : 'rgba(148, 0, 211, 0.6)'
                  },

                ]
            }
        }
    }

    componentDidUpdate(){
        let lc = this.chartReference.chartInstance;
        lc.config.data.labels = this.props.test.dt;
        lc.config.data.datasets[0].data = this.props.test.val1;
        lc.update();
        let lc2 = this.chartReference2.chartInstance;
        lc2.config.data.labels = this.props.test.dt;
        lc2.config.data.datasets[0].data = this.props.test.val2;
        lc2.update();
    }


    render(){
        const { classes } = this.props;
          return(
              <div>
                <Grid container  spacing={3}>
                    <Grid item className={classes.grid2} xs={12} sm={6} >
                        <Card  align="center">
                          <Grid container spacing={0} className={classes.grid}>
                            <Grid item xs={12} sm={5}>
                              <ReactSpeedometer
                                  maxValue={20}
                                  value={this.props.Onedata.val1}
                                  needleColor="black"
                                  segments={4}
                                  segmentColors={['red','yellow','green','blue']}
                                  ringWidth={40}
                                  height={200}
                                  width={300}
                                  />
                            </Grid>
                            <Grid item  xs={12} sm={7}>  
                              <div className={classes.Chart}>
                              <Line 
                                data = {this.state.chartData}
                                width = {500}
                                height= {250}
                                options = {
                                    {maintainAspectRatio: false}           
                                }
                                ref = {(reference) => this.chartReference = reference}
                                />
                                </div>
                            </Grid>
                          </Grid>
                          <Typography  className={classes.title}>
                            PT3801
                          </Typography>  
                        </Card>
                    </Grid>
                    <Grid item className={classes.grid2} xs={12} sm={6} >
                        <Card align="center">
                        <Grid container spacing={0} className={classes.grid}>
                            <Grid item xs={12} sm={5}>
                              <ReactSpeedometer
                                  needleHeightRatio={0.7}
                                  maxValue={50}
                                  customSegmentStops={[
                                      0,
                                      20,
                                      30,
                                      40,
                                      50
                                  ]}
                                  segmentColors={[
                                      'firebrick',
                                      'tomato',
                                      'gold',
                                      'limegreen'
                                  ]}
                                  value={this.props.Onedata.val2}
                                  height={200}
                              />
                            </Grid>
                            <Grid item  xs={12} sm={7}>  
                              <div className={classes.Chart}>
                              <Line 
                                data = {this.state.chartData2}
                                width = {500}
                                height= {250}
                                options = {
                                    {maintainAspectRatio: false}           
                                }
                                ref = {(reference) => this.chartReference2 = reference}
                                />
                                </div>
                            </Grid>
                          </Grid>
                          <Typography className={classes.title}>
                              TE3801
                          </Typography>
                        </Card>
                    </Grid>
                </Grid>
                </div>
          )
      }
     
}

export default  withStyles(styles)(RTChart);